.form-compare-section {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  min-height: calc(100vh - 32.9rem);
}

.form-compare-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: flex-start;
}

.form-compare-content {
  border: 1px solid var(--primary_background);
  border-radius: 2.5rem;
  padding: 2rem 4rem;
  height: auto;
  min-height: 37rem;
}

.form-compare {
  margin-bottom: 2.5rem;
}

.form-compare h1 {
  color: var(--heading_primary);
}

.form-compare p {
  color: var(--heading_secondary);
}

/* Card compare */
.form-compare-card-container {
  display: flex;
  width: 100%;
  color: var(--text_dark);
  margin-top: 2rem;
}

.form-compare-card-container p span {
  font-weight: bold;
}

.form-compare-card-content {
  width: 100%;
  border: 1px solid var(--primary_background);
  padding: 1.5rem;
  transition: all .4s;
  cursor: pointer;
}

.active-card {
  border: 1px solid var(--tertiary_background);
  background-color: var(--fourth_background);
}

.bestMatch-card {
  /* border: 2px solid var(--tertiary_background); */
  border: 2px solid var(--tertiary_background);
}

.sticky {
  position: relative;
  top: 36px;
  right: -40px;
  color: var(--white);
  background-color: var(--primary_background);
  display: inline-block;
  justify-content: center;
  font-weight: bold;
  font-size: 1.4rem;
}

.sticky p {
  margin: 0;
}

.form-compare-card-column {
  margin: 0 auto;
}

.form-compare-card-row {
  display: flex;
  flex-direction: column;
}

.form-compare-card-image {
  text-align: center;
}

.form-compare-card-image img {
  width: 130px;
}

.form-compare-card-info {
  text-align: center;
}

.form-compare-card-info h2 {
  font-size: 2rem;
}

.form-compare-card-rating-container p {
  margin: 0;
}

.form-compare-card-fees,
.form-compare-card-fixed,
.form-compare-card-cost {
  display: flex;
  justify-content: space-evenly;
}

.form-compare-card-fees p,
.form-compare-card-fixed p,
.form-compare-card-cost p {
  display: flex;
  justify-content: space-evenly;
  margin: 0 0 1.5rem 0;
}

/* Sign up */
.form-compare-card-estimated-container {
  padding: 1rem 0;
  margin: 0 1rem;
  border-radius: .3rem;
  background-color: var(--primary_background);
}

.form-compare-card-estimated {
  display: flex;
  flex-direction: column;
}

.form-compare-card-estimated p {
  color: var(--white);
  margin: 0;
}

.form-compare-card-estimated-amount {
  font-weight: bold;
  font-size: 3.5rem;
}

.form-compare-card-estimated-amount span {
  font-size: 2rem;
}

.form-compare-card-btn-signup-container {
  display: flex;
  justify-content: center;
  margin: 2rem 0 2.5rem 0;
}

/* Buttons */
.form-compare-card-btn-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.form-compare-card-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 4rem;
  margin: 0.5rem;
}

.form-compare-card-btn-custom {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95%;
  cursor: pointer;
  height: 4rem;
  margin: 0.5rem;
}

.form-compare-btn-container {
  display: flex;
  justify-content: space-between;
  margin-top: 2.5rem;
}

.btn-signup {
  background-color: var(--tertiary_background);
  color: var(--text_white);
  font-weight: bold;
  border: 1px solid var(--tertiary_background);
  transition: all 0.4s;
}

.btn-signup:hover {
  background-color: #b71390;
  border: 1px solid var(--tertiary_background);
  font-size: 1.4rem;
}

.btn-tariff {
  background-color: var(--white);
  border: 1px solid var(--tertiary_background);
  color: var(--tertiary_background);
  font-weight: bold;
  transition: all 0.4s;
}

.btn-tariff:hover {
  background-color: #f9f3f3;
  font-size: 1.4rem;
}

.form-compare-card-btn-info {
  margin-top: 1rem;
  text-align: center;
}

.form-compare-card-btn-info p {
  margin: 0 auto;
}

.form-compare-loadmore-btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4rem;
  margin-top: 1rem;
}

.form-compare-loadmore-btn {
  padding: .5rem 1rem; 
  background-color: var(--primary_background);
  border-radius: 2.5rem;
  color: var(--text_white);
  font-weight: bold;
  border: 1px solid var(--primary_background);
  cursor: pointer;
  transition: all 0.4s;
}

.form-compare-loadmore-btn:hover {
  color: var(--primary_background);
  background-color: var(--white);
}

/* Filter By */
.filterBy-container {
  display: flex;
  justify-content: end;
  align-items: center;
  align-content: center;
}

.filterBy-container label {
  color: var(--text_dark);
  margin: 0 1rem 0 0;
}

.filterBy-container select {
  padding: .5rem;
  text-align: center;
}

/* Continue && Go back */
.form-compare-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 4rem;
}

.btn-next {
  background-color: var(--tertiary_background);
  color: var(--text_white);
  font-weight: bold;
  padding-left: 5rem;
  padding-right: 2.5rem;
  border: 1px solid var(--tertiary_background);
  transition: all 0.4s;
}

.btn-next:hover {
  background-color: var(--white);
  border: 1px solid var(--tertiary_background);
  color: var(--primary_color);
}

.icon-next {
  transition: all 0.4s;
}

.btn-next:hover .icon-next {
  fill: var(--tertiary_background);
}

.btn-back {
  background-color: var(--white);
  border: 1px solid var(--tertiary_background);
  color: var(--tertiary_background);
  font-weight: bold;
  padding-left: 2.5rem;
  padding-right: 5rem;
  transition: all 0.4s;
}

.icon-back {
  transition: all 0.4s;
}

.btn-back:hover {
  background-color: var(--tertiary_background);
  color: var(--text_white);
}

.btn-back:hover .icon-back {
  fill: var(--white);
}

/* Script */
.script-compare {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50rem;
  margin-top: 2.5rem;
}

/* Assistant */
.assistant-compare {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 30rem;
  margin-top: 2.5rem;
}

.assistant-compare-custom {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 30rem;
  margin-top: 2.5rem;
}

.helper-compare {
  margin: 0 auto;
}

/* Tariff info */
.swal-modal_custom {
  color: var(--primary_color);
}

.form-compare-details-info-img img {
  width: 12rem;
  margin-bottom: 1rem;
}

.form-compare-details-container { 
  display: flex;
  flex-direction: column;
}

.form-compare-details-info h3 {
  text-align: start;
  /* margin-left: 1rem; */
  margin-top: 2.8rem;
  font-size: 2.2rem;
  text-decoration: underline;
}

.form-compare-single-details {
  display: flex;
  color: var(--text_dark);
  padding: .5rem;
  border-bottom: .5px solid rgba(49, 20, 91, .2);
}

.form-compare-single-details p {
  font-weight: bold;
  margin: 0;
}

.form-compare-single-details span {
  font-weight: normal;
  margin-left: 1rem;
}

/** MEDIAQUERIES*/
@media (min-width: 576px) {
}

@media (min-width: 768px) {
  .form-compare-section {
    width: 750px;
  }

  .assistant-compare-custom {
    margin-left: 2rem;
    height: 50rem;
  }

  .helper-compare {
    display: flex;
  }

  .form-compare-card-row {
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }

  .form-compare-card-fees,
  .form-compare-card-fixed,
  .form-compare-card-cost {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-content: center;
  }

  .form-compare-card-fees p,
  .form-compare-card-fixed p,
  .form-compare-card-cost p {
    margin: 0;
  }

  .form-compare-card-content {
    display: flex;
    align-items: center;
  }

  .form-compare-card-supplier {
    display: flex;
    align-items: center;
  }

  .form-compare-card-image {
    padding: 0 1rem;
  }

  .form-compare-card-info {
    padding: 0 0 0 3rem;
  }

  .form-compare-card-btn {
    width: 15rem;
  }
}

@media (min-width: 992px) {
  .form-compare-section {
    width: 970px;
  }

  .form-compare-container {
    flex-direction: row;
  }

  .script-compare {
    margin-top: 0;
    margin-left: 2rem;
  }

  .assistant-compare {
    margin-top: 0;
    margin-left: 2rem;
  }

  .assistant-compare-custom {
    margin-left: 2rem;
    height: 35rem;
  }

  .helper-compare {
    width: 100%;
    flex-direction: column;
  }
}

@media (min-width: 1200px) {
  .form-compare-section {
    width: 1170px;
  }
}